import { Component, OnInit, AfterViewInit, OnDestroy, ChangeDetectionStrategy, ViewChild, Input } from '@angular/core';
import { Router } from '@angular/router';

import { Observable, Subject } from 'rxjs';
import { tap, map, takeUntil } from 'rxjs/operators';

import { MatSnackBar, SimpleSnackBar, MatSnackBarRef } from '@angular/material';
import { MatTable, MatTableDataSource } from '@angular/material/table';

import { UserWithTokenInterface } from '../../auth/user.interface';
import { LoanApplicationInterface } from '../loanapplication.interface';
import {
  IsEntityChangedInterface, EntityMessageTypeEnum, EntityStateEnum,
  EntityChangeResultEnum, OpStateInterface
}
  from '../../shared/model/entity-state.interface';

import { EntityStateService } from '../../shared/services/entity-state.service';
import { UserService } from '../../auth/user.service';
import { LoanApplicationListService } from '../loanapplication-list.service';

import { LoanApplicationTableComponent } from './loanapplication-table/loanapplication-table.component';
import { isNgTemplate } from '@angular/compiler';

@Component({
  selector: 'app-loanapplication-list',
  templateUrl: './loanapplication-list.component.html',
  styleUrls: ['./loanapplication-list.component.scss'],
  providers: [
    EntityStateService
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,

})
export class LoanApplicationListComponent implements OnInit, AfterViewInit, OnDestroy {
  
  // @ViewChild(LoanApplicationTableComponent, {static: false}) loanApplicationTableCmp: LoanApplicationTableComponent;

  public currentUser$: Observable<UserWithTokenInterface> = null;
  public currentLoanApplications$: Observable<any[] | LoanApplicationInterface[]> = null;
  
  // public curLoanApplicationList$: Observable<LoanApplicationInterface[]> = null;

  // --------------------------------------------------------------
  private simpleSnackBarRef: MatSnackBarRef<SimpleSnackBar> = null;
  public isLoanApplicationsChanged$: Observable<OpStateInterface> = null;
  // --------------------------------------------------------------
  private onDestroySub$ = new Subject<boolean>();
  
  isFilter = false; // netesa
  
   private _loanApplicationView: string='';
   public get loanApplicationView(): string {
     return this._loanApplicationView;
   }
   public set loanApplicationView(val) {
      // console.log('LoanApplicationListComponent.loanApplicationView(val) val=%s', val);
      this._loanApplicationView = val;
   }
   private _renderLoanApplicationArr: LoanApplicationInterface[] = null;
   public get renderLoanApplicationArr() {
    return this._renderLoanApplicationArr;
   }
   public set renderLoanApplicationArr(val) {
     this._renderLoanApplicationArr = val;
   }


  constructor(
    private router: Router,
    protected userSrv: UserService,
    protected loanApplicationsSrv: LoanApplicationListService,
    protected loanApplicationsStateSrv: EntityStateService,
    private snackBarSrv: MatSnackBar
  ) {
    loanApplicationsStateSrv.context = "LoanApplicationListComponent";
  }
    
  ngOnInit() {
    this.currentUser$ = this.userSrv.curUser$
      .pipe(
        tap((user: UserWithTokenInterface) => {
          // console.log(`\tPIPE: LoanApplicationListComponent.OnInit() tap currentUser$:%O`, user);
          if (this.userSrv.isAnonymUser(user)) {
            this.onDestroySub$.next(true);  // befor!
            this.loanApplicationsSrv.clearLoanApplicationList();
            // this.onDestroySub$.next(true);
          }
        }),
        takeUntil(this.onDestroySub$)
      );
    this.currentLoanApplications$ = this.loanApplicationsSrv.curLoanApplicationList$
      .pipe(
        tap((loanApplications: any[]) => {
          // console.log(`\tPIPE: LoanApplicationListComponent.OnInit() tap currentLoanApplications$:%O`, loanapplications,);

          if (this.loanApplicationsSrv.isWrongLoanApplicationList(loanApplications)) {
            if(this.userSrv.isCurUserAuth()){
              this.loanApplicationsSrv.loadLoanApplicationList(this.loanApplicationsStateSrv);

            }
          }
        }),
        map((loanApplications: any[]) => {
          // console.log(`\tPIPE: LoanApplicationListComponent.OnInit() map currentLoanApplications$:%O`, loanApplications,);

          if (this.loanApplicationsSrv.isWrongLoanApplicationList(loanApplications)) {
            return null;
          }
          if (!Array.isArray(loanApplications) || loanApplications.length === 0) {
              return null;
          }
          return loanApplications;
        }),
        takeUntil(this.onDestroySub$)
      );
    this.isLoanApplicationsChanged$ = this.loanApplicationsStateSrv.isEntityChanged$
      .pipe(
          map((r: IsEntityChangedInterface): OpStateInterface   => {
          // console.log(`\tPIPE: LoanApplicationListComponent.OnInit() IsEntityChangedInterface:%O`, r,);
  
          if (r.op === EntityStateEnum.NOSET) {
            if (!r.isEnd && r.opResult === EntityChangeResultEnum.NOSET) {
              if (this.simpleSnackBarRef != null) {
                this.simpleSnackBarRef.dismiss();
                this.simpleSnackBarRef = null;
              }
              return {op: 'NOSET', isEnd: false, opResult: 'NOSET'};
            }
          } else if (r.op === EntityStateEnum.LOAD) {
            if (!r.isEnd && r.opResult === EntityChangeResultEnum.NOSET) {
              if (this.simpleSnackBarRef != null) {
                this.simpleSnackBarRef.dismiss();
                this.simpleSnackBarRef = null;
              }
              return {op: 'LOAD', isEnd: false, opResult: 'NOSET'};
            } else if (r.isEnd && r.opResult === EntityChangeResultEnum.ERROR) {
                this.simpleSnackBarRef = this.snackBarSrv.open(`${r.message}`,
                  'X', {
                  duration: 0,
                  panelClass: 'mat-snack-bar-container_err'
                });
                return {op: 'LOAD', isEnd: true, opResult: 'ERROR'};
           } else if (r.isEnd && r.opResult === EntityChangeResultEnum.SUCCESS) {
              return {op: 'LOAD', isEnd: true, opResult: 'SUCCESS'};
            }
          } else if (r.op === EntityStateEnum.DELETE) {
            if (!r.isEnd && r.opResult === EntityChangeResultEnum.NOSET) {
              if (this.simpleSnackBarRef != null) {
                this.simpleSnackBarRef.dismiss();
                this.simpleSnackBarRef = null;
              }
              return {op: 'DELETE', isEnd: false, opResult: 'NOSET'};
            } else if (!r.isEnd && r.opResult === EntityChangeResultEnum.ERROR) { // Error
              this.simpleSnackBarRef = this.snackBarSrv.open(`${r.message}`,
                'X', {
                duration: 0,
                panelClass: 'mat-snack-bar-container_err'
              });
              return {op: 'DELETE', isEnd: true, opResult: 'ERROR'};
            } else if (r.isEnd && r.opResult === EntityChangeResultEnum.SUCCESS) {
              this.simpleSnackBarRef =
                this.snackBarSrv.open(`Selected Loan Application has been deleted!`,
                'X', {
                duration: 0,
                panelClass: 'mat-snack-bar-container_info'
              });
              return {op: 'DELETE', isEnd: true, opResult: 'SUCCESS'};
            }
          }
        })
      );    
  // ngOnInit       
  }
  ngAfterViewInit() {


  }

  buttonToggleChange(ev: any) {
    // console.log('buttonToggleChange ev -> %O', ev);

  }

  filter() {
    this.isFilter = !this.isFilter;
  }
  
  loanApplicationViewChanged(ev: string) {
    // console.log('LoanApplicationListComponent.loanApplicationViewChanged(ev) view=%s', ev);
    this.loanApplicationView = ev;
  }
  
  renderLoanApplicationsChanged(ev: LoanApplicationInterface[]) {
     console.log('LoanApplicationListComponent.renderLoanApplicationsChanged(ev) loanApplicationArr=%O', ev);
    this.renderLoanApplicationArr = ev;
  }

  ngOnDestroy() {
    // this.loanApplicationsSrv.clearLoanApplicationList(); // auto loaded

    if (this.simpleSnackBarRef != null) {
      this.simpleSnackBarRef.dismiss();
      this.simpleSnackBarRef = null;
    }
  }
} 