import { Component, OnInit, ViewChild, Input, 
  EventEmitter, ChangeDetectionStrategy, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Observable } from 'rxjs';
import { tap, map } from 'rxjs/operators';

import { MatSort, Sort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';

import { UserWithTokenInterface } from '../../../auth/user.interface';
import { LoanApplicationInterface } from '../../loanapplication.interface';

import { IsEntityChangedInterface, EntityChangeResultEnum, 
        OpStateInterface } from '../../../shared/model/entity-state.interface';

import { LOANAPPLICATION_DATA } from './mock-data';

@Component({
  selector: 'app-loanapplication-table',
  templateUrl: './loanapplication-table.component.html',
  styleUrls: ['./loanapplication-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanApplicationTableComponent implements OnInit {
  public currentUser$: Observable<UserWithTokenInterface> = null;
  public currentLoanApplications$: Observable<LoanApplicationInterface[]> = null;
  public isLoanApplicationsChanged$: Observable<OpStateInterface> = null;

  displayedColumns: string[] = [
    'loanApplicationId',
    'productName',
    'clientState',
    'loanPurpose',
    'loanAmount',
    'firstName',
    'lastName',
    'phoneNumber',
    'email',

    'status',  // ??? makarov  status loanapplications.status
  ];

  selectedLoanApplication: LoanApplicationInterface | null;

  dataSource: MatTableDataSource<LoanApplicationInterface>;

  @Input('data-source') loanApplicationArr: LoanApplicationInterface[];
  
  private _loanApplicationView: string = '';
  @Input('loanapplication-view')
  get loanApplicationView() {
    console.log('LoanApplicationTableComponent.loanApplicationView() GET val -> %s', this._loanApplicationView);
    return this._loanApplicationView;
  }
  set loanApplicationView(val: string) {
     console.log('LoanApplicationTableComponent.loanApplicationView(val) SET val -> %s', val);
    this._loanApplicationView = val;
    this.dataSource.filter = val;
  }

  @ViewChild(MatTable, { static: true }) matTbl: MatTable<LoanApplicationInterface[]>;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  public renderLoanApplicationList$: Observable<LoanApplicationInterface[]> = null;
  
  @Output('render-loanapplications') renderLoanApplicationListChange = new EventEmitter<LoanApplicationInterface[]>();

  constructor(
    private router: Router,
    private route: ActivatedRoute

  ) {
    console.log('LoanApplicationTableComponent constructor() -> setting datasource');
    this.dataSource = new MatTableDataSource();
    // this.dataSource.sort = this.sort;
    this.dataSource.filterPredicate = (data, filter) => {
      // console.log('LoanApplicationTableComponent dataSource.filterPredicate() data=%O filter=%s',
      //               data, filter);

      // const dataStr = data.clientState.toLowerCase().trim();
      const dataStr = data.status.toLowerCase().trim();      // ??? makarov  status loanapplications.status
      return dataStr.indexOf(filter) != -1; 
    }
  }

  ngOnInit() {

    // this.dataSource = new MatTableDataSource(LOANAPPLICATION_DATA);  //MOCK OK!

    this.dataSource.data = this.loanApplicationArr;
    this.dataSource.sort = this.sort;
    
    const sortState: Sort = {active: 'loanApplicationId', direction: 'desc'};
    this.sort.active = sortState.active;
    this.sort.direction = sortState.direction;
    this.sort.sortChange.emit(sortState);


    this.renderLoanApplicationList$ = this.dataSource.connect()
      .asObservable()
      .pipe(
        tap((loanApplications: LoanApplicationInterface[]) => {
          // console.log('\tPIPE: LoanApplicationTableComponent.OnInit() renderLoanApplicationList:%O', loanApplications);
                    
          this.renderLoanApplicationListChange.emit(loanApplications);
        })
      );

    
  }
  onTblRowClick(loanApplication: LoanApplicationInterface) {
    // console.log(`\tPIPE: LoanApplicationTableComponent.OnInit() clicked Client:%O`, loanApplication);

    if (loanApplication.clientState == 'Available' || loanApplication.clientState == 'Active' || loanApplication.clientState == 'In Process') {
      this.router.navigate([`/clients`, loanApplication.clientId, 'loans']);
    }else {
      this.router.navigate([`/clients`, loanApplication.clientId, 'loanapplications']);
    }

    // this.router.navigate([loanApplication.loanApplicationId, 'overview'], {relativeTo: this.route});
  }
}