import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { of, Subject } from 'rxjs';
import { map, catchError, takeUntil, shareReplay, startWith } from 'rxjs/operators';

import { LoanApplicationInterface, WrongLoanApplicationList } from './loanapplication.interface';

import { EntityStateService } from '../shared/services/entity-state.service';
import { IsEntityChangedInterface, EntityMessageTypeEnum, 
  EntityStateEnum, EntityChangeResultEnum } from '../shared/model/entity-state.interface';

import {ServerStatusService } from '../shared/services/server-status.service';

import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class LoanApplicationListService {
  private apiUrl = environment.apiUrl;

  private curLoanApplicationListSub$ = new Subject<any[] | LoanApplicationInterface[]>();
  public curLoanApplicationList$ = this.curLoanApplicationListSub$.asObservable()
                        .pipe(
                          startWith(WrongLoanApplicationList),
                          shareReplay(1)
                        );
  public curLoanApplicationList: LoanApplicationInterface[] = WrongLoanApplicationList;
  
  private onDestroySub$ = new Subject<boolean>();

  constructor(
    private http: HttpClient,
    private serverStatusSrv: ServerStatusService,
    ) {
      this.curLoanApplicationList$.subscribe(
        loanApplications => {
          this.curLoanApplicationList = loanApplications;
        }
      );
  }
  // ------------------------------------------------------------
  public isWrongLoanApplicationList(loanApplications: LoanApplicationInterface[]): boolean {
    if (!loanApplications) {
      throw new Error('Parameter [loanApplications] is null');
    }
    if (loanApplications === WrongLoanApplicationList) {
      return true;
    } else {
        return false;
    }
  }  
  public isLoanApplicationListNotEmpty(loanApplications: LoanApplicationInterface[]): boolean {
    if (!loanApplications) {
      throw new Error('Parameter [loanApplications] is null');
    }
    if (loanApplications === WrongLoanApplicationList) {
      return false;
    } else {
        return true;
    }
  }
  private isLoanApplicationListEmpty(): boolean {
    return !this.isLoanApplicationListNotEmpty(this.curLoanApplicationList);
  }
  public clearLoanApplicationList(): void {
    if (!this.isLoanApplicationListEmpty()) {
      this.curLoanApplicationListSub$.next(WrongLoanApplicationList);
    }
  }
// ------------------------------------------------------------
public loadLoanApplicationList(stateService: EntityStateService): void {
  // console.log('SYNC ENTER LoanApplicationListService.loadLoanApplicationList() %O', stateService);

  if (stateService) {
    const isLoanApplicationsChangedEnter: IsEntityChangedInterface = {
      op: EntityStateEnum.LOAD,
      isEnd: false,
      opResult: EntityChangeResultEnum.NOSET
    };
    stateService.next(isLoanApplicationsChangedEnter);
  }
  this.http.get<any>(`${this.apiUrl}/loanapplications`, {
    headers: new HttpHeaders()
      // .set("Content-Type", "application/json")
      .set("Accept", "application/json")
  })
  .pipe(
    map((loanApplications: any) => {
      // console.log('\tASYNC PIPE LoanApplicationListService.loadLoanApplicationList().map(ANY): %O', loanApplications);
      if (this.isWrongLoanApplicationList(loanApplications)) {
        return null;
      }
      if (!Array.isArray(loanApplications) || loanApplications.length === 0) {
        return null;
      }  
      let curLoanApplicationList = loanApplications.map(item => {
        let loanApplication: LoanApplicationInterface = {
          productName : item.productName,
          loanApplicationId: item.id.toString(),
          loanPurpose: item.loanPurpose,
          firstName: item.client.profile.firstName,
          lastName: item.client.profile.lastName,
          phoneNumber: item.client.profile.mobilePhoneNumber,
          email: item.client.profile.emailAddress,
          clientId: item.client.id,
          clientState: item.client.status,          // ??? makarov  clientState = client.status       
          loanAmount: item.loanAmount,
          feesDue: item.feesDue,
          penaltyDue: item.penaltyDue,
          creditOfficer: item.client.loanOfficer,   // ??? makarov  loanapplications.officers: []

          status: item.status      // ??? makarov  status = loanapplications.status
        };
        return loanApplication;
      })


      return {token: 'OK', loanApplications: curLoanApplicationList};
    }),
    catchError((err: any): any =>{
      // console.log('\tASYNC PIPE LoanApplicationListService.loadLoanApplicationList().catchError() #2: %O', err);

      let msg: string;
      let isLoanApplicationsChangedError: IsEntityChangedInterface = null;
      if (stateService) {
        isLoanApplicationsChangedError = {
          op: EntityStateEnum.LOAD,
          isEnd: true,
          opResult: EntityChangeResultEnum.ERROR,
        };
      }
      if (err instanceof HttpErrorResponse) {
        // Server error 
        const status = err.status;
        if (status === 0) {
          msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
        } else if (status > 0 && status < 600) {
            msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
        }
        isLoanApplicationsChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isLoanApplicationsChangedError.message = msg;
      } else {
        // LoanApplication error
        msg = err.message ? err.message : err.toString(); ;
        isLoanApplicationsChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isLoanApplicationsChangedError.message = `LOANAPPLICATION ERROR: ${msg}`;
      }
      if (stateService) {
        stateService.next(isLoanApplicationsChangedError);
      }      
      this.clearLoanApplicationList();
      this.onDestroySub$.next(true);
      
      return of({token: 'ERROR', loanApplications: null});
    }),
    takeUntil(this.onDestroySub$)
  ).subscribe(
    (rezObj: {token: string; loanApplications: any[]} ) => {
      // console.log(`\tSUBSCRIBE: LoanApplicationListService.loadLoanApplicationList().subscribe(NEXT): rezObj: %O`, rezObj);

      // if (rezObj.token === 'ERROR') {
      //   if (stateService) {
      //     const isLoanApplicationsChangedError: IsEntityChangedInterface = {
      //       op: EntityStateEnum.LOAD,
      //       isEnd: true,
      //       opResult: EntityChangeResultEnum.ERROR,
      //     };
      //     stateService.next(isLoanApplicationsChangedError);
      //   }
      //   this.clearLoanApplicationList();
      // } else
       if (rezObj.token === 'OK') {
        this.curLoanApplicationListSub$.next(rezObj.loanApplications); // BEFOR stateService.next()!
        if (stateService) {
          const isLoanApplicationsChangedExit: IsEntityChangedInterface = {
            op: EntityStateEnum.LOAD,
            isEnd: true,
            opResult: EntityChangeResultEnum.SUCCESS
          };
          stateService.next(isLoanApplicationsChangedExit);
        }
        // this.curLoanApplicationListSub$.next(rezObj.loanApplications);
      }
    }
    // , err => console.log(`\tSUBSCRIBE: LoanApplicationListService.loadLoanApplicationList().subscribe(ERROR): %O`, err)
    // , () => console.log(`\tSUBSCRIBE: LoanApplicationListService.loadLoanApplicationList().subscribe(COMPLETE)`)
  );

}

}
