import {
  Component, OnInit, Input, Output,
  ChangeDetectionStrategy, EventEmitter
} from '@angular/core';

import { MatButtonToggleChange } from '@angular/material/button-toggle';

import * as _ from 'lodash'; 

import { LoanApplicationInterface } from '../../loanapplication.interface';



@Component({
  selector: 'app-loanapp-filter-settings',
  templateUrl: './loanapp-filter-settings.component.html',
  styleUrls: ['./loanapp-filter-settings.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanappFilterSettingsComponent implements OnInit {

  @Input('data-source') loanApplicationArr: LoanApplicationInterface[];
  @Output('loanapplication-view-change') loanApplicationViewChange = new EventEmitter<string>();

  private _renderLoanApplicationArr: LoanApplicationInterface[] = null;
  @Input('render-data')   
    public get renderLoanApplicationArr() {
      return this._renderLoanApplicationArr;
    }
    public set renderLoanApplicationArr(val) {
      this._renderLoanApplicationArr = val;
      this.officerList = _.uniqBy(val, 'creditOfficer')
                          .map(item => item.creditOfficer);
      // this.cityList = _.uniqBy(val, 'city')
      //                   .map(item => item.city);
      const arr =  _.sortBy(val, 'city')
                        .map(item => item.city);
      this.cityList = _.uniq(arr);
    }
  
  // public OfficerList = CREDIT_OFFICER_LIST;
  public officerList: string[] = [] ;
  public cityList: string[] = [] ;

  private _selView = 'all';
  public get selectedView() {
    return this._selView;
  };
  public set selectedView(val: string) {
    console.log('FilterSettingsComponent.selectedView(val) SET val=%s', val);
    this._selView = val;
    if (val === 'all') {
      val = '';
    }
    this.loanApplicationViewChange.emit(val);
  }

  public isFilterUse = false;
  public isShareUse = false;

  public isViewTwoPanel = false;

  constructor(

  ) {

  }

  ngOnInit() {

    this.selectedView = 'requested';
  }

  onToggleBtnChange(e: MatButtonToggleChange) {

    this.isFilterUse = !this.isFilterUse;

    if (this.isFilterUse) {
      if (!this.isViewTwoPanel) {
        this.isViewTwoPanel = true;
      }
      else {
        // this.isViewTwoPanel = true;
      }
    }

    if (!this.isFilterUse) {
      if (!this.isShareUse) {
        this.isViewTwoPanel = false;
      }
    }
    // console.log('FilterSettingsComponent.onToggleBtnChange(event) isFilterUse -> %O', this.isFilterUse);
    // console.log('FilterSettingsComponent.onToggleBtnChange(event) isShareUse -> %O', this.isShareUse);
    // console.log('FilterSettingsComponent.onToggleBtnChange(event) isViewTwoPanel  1 -> %O', this.isViewTwoPanel);
  }

  onToggleBtnChangeShare(e: MatButtonToggleChange) {

    this.isShareUse = !this.isShareUse;

    if (this.isShareUse) {
      if (!this.isViewTwoPanel) {
        this.isViewTwoPanel = true;
      }
      else {
        // this.isViewTwoPanel = true;
      }
    }

    if (!this.isShareUse) {
      if (!this.isFilterUse) {
        this.isViewTwoPanel = false;
      }
    }
    // console.log('FilterSettingsComponent.onToggleBtnChange(event) isFilterUse -> %O', this.isFilterUse);
    // console.log('FilterSettingsComponent.onToggleBtnChange(event) isShareUse -> %O', this.isShareUse);
    // console.log('FilterSettingsComponent.onToggleBtnChange(event) isViewTwoPanel  1 -> %O', this.isViewTwoPanel);
  }



}
